@import '../../../variable';

$combobox-height: 20px;
$combobox-margin: 2px;
$combobox-padding: 2px;
$combobox-border: 2px solid $base-color-dark;
$combobox-down-arror-width: 20px;
$combobox-input-width: 140px;

:host {
    display: inline-block;
    position: relative;
    width:100%;
        box-sizing: border-box;

}

.combobox {
//    @include flex-child($grow: 1);
//    @include flex-main(column);
//    @include flex-aling(flex-start, flex-center);

//    height: $combobox-height;
//    max-height: $combobox-height;
    width: 100%;
    position: relative;
    /*border: 1px solid #222;*/
}

.combobox-toogle {
    @include flex-main(row);
    @include flex-aling(space-between, center);
    // @include flex-child(($combobox-input-width + $combobox-down-arror-width) );
    position: relative;
    background-color: $base-color-dark;
  
}

.combobox-input {
    // @include flex-main(row);
    // max-width: 10em;
    @include flex-child($grow: 1);
    display: inline-block;

    // min-width:$combobox-input-width; 
    // border: none;
    // min-width: $combobox-input-width;
    width: calc( 100% - #{$combobox-down-arror-width} );
        margin: 0;
        box-sizing: border-box;

}

.combobox-caret {
    @include flex-child($combobox-down-arror-width);
    // align-self: center;
    display: inline-block;
    margin: 0;
        width: $combobox-down-arror-width - 4px;
    cursor: pointer;
    color: $background-color;
    text-align: center;

}



.combobox-menu {
    @include flex-main (column);
    @include flex-aling(space-between, stretch);
    // display: flex;
    position: absolute;
    top:  $combobox-height;
    border: 1px solid $base-color-dark;
        // flex-direction: column;
    // align-items: stretch;
    // justify-content: space-between;

    z-index: 100;

    // max-height: 200px;
    min-width: 200px;
    // overflow: auto;

    background-color: $base-color-light;
    color: $base-color-dark;  

   list-style-type: none;
    margin: 0;
    padding: 0;
    // overflow: hidden;
}

.combobox-item {
    padding: 4px 4px;
}

.combobox-toogle:hover,
.combobox-item:hover {
    // color: #d6d;
    // outline: 2px solid #d6d;
    color: $base-hover-color;
    outline: 2px solid $base-hover-color;
}

.combobox-item:active,
.combobox-toogle:active,
.active {
    // color: #222;
     background-color: $base-active-color;

    //  outline: 2px solid #d6d;
        color: $base-color-light;
    outline: 2px solid $base-color-light;

}

// .combobox:hover .combobox-menu {
//     display: flex;
//     outline: 2px solid #d6d;
// }
.label-combobox  + .active {
    color: $base-active-color;
    background-color: none;
    outline: none;
}

.is-data {
    // background-color: #6dd;
    // outline: 2px solid #6dd;
    color: $base-active-color;
    border: 2px solid $base-active-color;
    background-color: none;
}
.is-data.label-combobox {
    // background-color: #6dd;
    // outline: 2px solid #6dd;
    border: none;
}